@import "sass/index";

$primaryIndent: 10px;

.switchWrapper {
  display: flex;
  align-items: center;
  & > span:first-child {
    margin-right: $primaryIndent;
  }
  & > span:last-child {
    margin-left: $primaryIndent;
  }
}

.option {
  text-transform: uppercase;
  color: use(--template-gameCell-switch-text);
  font-size: 14px;
  font-weight: bold;
  cursor: pointer;
  user-select: none;
}

.highlight {
  color: use(--template-gameCell-switch-selectedText);
}

.switchTrack {
  height: 13px;
  width: 90%;
}
