@import "sass/index";
@import "config/themes/_diamonds";

$inputHeight: 60px;
$inputFieldHeight: 40px;
$primaryIndent: 10px;

$coinImageSize: 26px;

$primaryFont: "Gilroy";

.root {
  position: relative;
}

.topLabel {
  display: block;
  font-family: $primaryFont;
  font-weight: bold;
  font-size: 14px;
  line-height: 17px;
  color: use(--template-input-topLabel-color);
  margin-bottom: 5px;
  & > span {
    display: inline-block;
    width: 50%;
  }
}

.topLabelTitle {
  text-align: start;
}

.topLabelValue {
  text-align: end;
}

.wrapper {
  display: flex;
  align-items: flex-end;
  width: 100%;
  height: $inputHeight;
  position: relative;
  overflow: hidden;
  background: rgba(white, 0.05);
  border: 1px solid $gunPowder;
  border-radius: 8px;

  & > div > label {
    line-height: 16px;
    font-size: 14px;
    font-family: "Varela Round";
    color: use(--template-input-topLabel-color);
    opacity: 0.5;
    left: $primaryIndent;
    top: 14px;
  }

  & > div {
    margin-top: 0;
  }
}

.input {
  margin-top: 0;
  border: 0;
  background: transparent;
  height: $inputFieldHeight;
  box-shadow: none !important;
  font-size: 18px;
  color: use(--template-input-topLabel-color);
  align-self: center;
}

.inputRoot {
  background-color: transparent;
}

.inputRoot {
  width: 100%;
}

.inputWithoutLabel {
  height: $inputHeight;
}

.inputWithLeftwardLabel {
  & > input {
    padding-left: #{$primaryIndent + 20px};
  }
}

.disabled {
  color: rgba(#ffffff, 0.6);
}

.btnsWrapper {
  position: absolute;
  height: 40px;
  display: flex;
  align-items: center;
  right: $primaryIndent;
  bottom: $primaryIndent;
  & > svg {
    cursor: pointer;
  }
  & > button {
    height: 40px;
    min-width: 40px;
    width: 45px;
    background-color: $gunPowder;
    font-family: $primaryFont;
    font-weight: bold;
    font-size: 12px;
    line-height: 15px;
    color: rgba(#ffffff, 0.5);
    border-radius: 6px;
  }
  & > button:not(:last-child) {
    margin-right: 2px;
  }
  @media (max-width: 380px) {
    & > button {
      display: none;
    }
  }
}

.coinImg {
  position: static;
  user-drag: none;
  width: $coinImageSize;
  height: $coinImageSize;
}

.leftwardCoinIconWrapper {
  display: flex;
  align-items: center;
  position: absolute;
  bottom: 0;
  left: $primaryIndent;
  height: $inputHeight;
}

.mobileButtons {
  display: none;
  & button {
    width: 100%;
    background-color: $gunPowder;
    color: rgba(white, 0.5);
    &:not(:last-child) {
      margin-right: 2px;
    }
  }
  @media (max-width: 380px) {
    display: flex;
    margin-top: 5px;
  }
}
