.buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.manualInput {
  display: inline-block;
  & > div {
    flex-grow: 1;
    width: 460px;
    margin-right: 20px;
  }
  @media (max-width: 1165px) {
    display: block;
    margin-right: 0;
    margin-bottom: 20px;
    width: 100%;
    & > div {
      width: 100%;
      flex-grow: 1;
    }
  }
}

.betControl {
  margin-top: 10px;
  & > button {
    position: relative;
    bottom: 2;
    flex-grow: 0;
    flex-shrink: 0;
    max-height: 50;
  }
  @media (max-width: 1165px) {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.autoBetBtn {
  display: flex;
  justify-content: center;
}

.warning {
  top: -30px;
}
